<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import request from '@/utils/request'


export default {

  name: "SearchTime",
  data() {
    return {
      keyWord: '',

      isFull: true,
      imgindex: 0,
      imgselect: {},
      isdtpageshow: false,
      thesize: { dx: 0, dy: 0, width: 0, height: 0 },

      page: 1,
      length: 1,
      total: 1,

      info: {
        isFirst: true,
        isLoading: false,
        errMsg: "",
        users: [],
      },
    };
  },

  computed: {
    ...mapState('favorites', { lovelistids: 'lovelistids' }),

    ...mapState('settings', { Defaultstate: 'Defaultstate' }),

    unsearched() {
      if (this.total == 0) {
        return 1
      } else {
        return null;
      }

    }
  },
  methods: {

    ...mapActions('favorites', {
      favorite_item: "FAVORITE_IT"
    }),

    // favorites
    ...mapMutations('favorites', {
      CONNECT: "CONNECT_IDB",
      ADD: "ADD_OBJECT",
      DELET: "DELETE_OBJECT"
    }),

    search() {
      // ËØ∑Ê±ÇÂâçÊõ¥Êñ∞ ListÁöÑÊï∞ÊçÆ
      this.info = { isFirst: false, isLoading: true, errMsg: "", users: [] }
      request({
        method: 'GET',
        url: `img/SourceController/getAllSource/${this.page}/${this.Defaultstate.size}?description=${this.keyWord}`
      })
        .then(response => {
          this.info = { isLoading: false, errMsg: "", users: response.data.data.records };
          this.length = response.data.data.pages
          this.total = response.data.data.total
        })
        .catch(error => {
          this.info = { isLoading: false, errMsg: error.message, users: [] };
        })
    },


    favorState(item) {
      if (this.lovelistids.includes(item.id)) {
        return true;
      } else {
        return false;
      }
    },

    // ‰∏ä‰∏ÄÂº†
    backone() {

      if (0 < this.imgindex) {
        this.imgindex--;
        this.imgselect = this.info.users[this.imgindex];
        this.canvasco();
        console.log(this.imgindex);
      } else if (0 == this.imgindex) {
        console.log("Â∑≤ËææÂà∞Á¨¨‰∏ÄÂº†ÔºåÊó†Ê≥ïËøõË°å‰∏Ä‰∏™Âáè");
      } else {
        console.log("ÂÖ∂‰ªñÈîôËØØÔºÅ");
      }

    },

    // ÂàáÊç¢ÂÖ®Â±èÂäüËÉΩ
    toggleFullScreen() {
      if (!document.fullscreenElement) {
        // ÂÖ®Â±èÂäüËÉΩüëá,ËøõË°å‰∏Ä‰∏™Á™óÂè£ÂÖ®Â±è, ËèúÂçïÊ†èÁä∂ÊÄÅÂèòÊç¢
        document.documentElement.requestFullscreen();

        this.isFull = false;
      } else {
        // ÈÄÄÂá∫ÂÖ®Â±èÂäüËÉΩüëáÔºåËøõË°å‰∏Ä‰∏™Á™óÂè£ÈÄÄÂá∫ÂÖ®Â±è
        if (document.exitFullscreen) {
          document.exitFullscreen();

          this.isFull = true;
        }
      }
    },

    // ‰∏ã‰∏ÄÂº†
    nextone() {
      if (this.imgindex < Number(this.info.users.length) - 1) {
        this.imgindex++;
        this.imgselect = this.info.users[this.imgindex];
        this.canvasco();

      } else if (this.imgindex == Number(this.info.users.length) - 1) {
        console.log("Â∑≤ËææÂà∞ÊúÄÂêé‰∏ÄÂº†ÔºåÊó†Ê≥ïËøõË°å‰∏Ä‰∏™ + üòµ");
      } else {
        console.log("ÂÖ∂‰ªñÈîôËØØÔºÅ");
      }
    },


    F11key() {
      document.documentElement.requestFullscreen();
    },

    fulltoexit() {
      this.isFull = false
      this.isF11 = true
    },

    exittofull() {
      this.isFull = true
      this.isF11 = false
      if (document.fullscreenElement) {
        document.exitFullscreen()
      }


    },


    updatesdetail(item) {
      const htmlStyle = document.getElementsByTagName('html')[0].style;
      htmlStyle.overflowY = 'hidden';

      this.imgselect = item;

      this.imgindex = this.info.users.indexOf(item)

      // Êõ¥Êñ∞È°µÈù¢Áä∂ÊÄÅ
      this.opendetailpage();
    },

    opendetailpage() { this.isdtpageshow = !this.isdtpageshow; },


    lol() {
      this.search()
    },

    // ÂÖ≥Èó≠ÊåâÈíÆ
    closedetailpage() {

      this.exitfullscreen();
      // this.opendetailpage();
      this.isdtpageshow = false

      // Ë∞ÉÊï¥ html overflow Â±ûÊÄß
      const htmlStyle = document.getElementsByTagName("html")[0].style;
      htmlStyle.overflowY = "";
    },

    // ÈÄÄÂá∫ÂÖ®Â±è
    exitfullscreen() {
      if (document.fullscreenElement) {
        console.log("ÂÖ®Â±èÁä∂ÊÄÅÔºåÈÄÄÂá∫ÂÖ®Â±è");
        document.exitFullscreen();

        this.isFull = true;
      }
    },

    /**
* ÁîªÂ∏ÉcanvasËÆæÁΩÆ
* ÁªòÂà∂,‰º†ÂÖ•thesizeÔºåË¶ÅÈáçÊñ∞ÁªòÂà∂ÁöÑÂèÇÊï∞
*/
    canvasco() {

      const canvas = document.querySelector('canvas');
      const ctx = canvas.getContext('2d')
      const thing = new Image();

      // ÊääÈÄâÊã©ÁöÑÂØπË±°ÂõæÂÉèË∑ØÂæÑ‰º†ÂÖ• Êñ∞Âª∫ÁöÑÂØπË±°thing
      thing.src = this.imgselect.src;




      // ÊåÇËΩΩÂõæÂÉèÂØπË±° thing
      thing.onload = function () {
        const size = resize();
        // console.log("thing", size.dx, size.dy, size.width, size.height)
        ctx.drawImage(thing, size.dx, size.dy, size.width, size.height)
      }

      window.onresize = function () {
        // console.log("ÈáçÊñ∞ÂÆö‰πâÂ§ßÂ∞è")
        const size = resize();
        ctx.drawImage(thing, size.dx, size.dy, size.width, size.height)
      }

      function resize() {
        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;

        // console.log("window", canvas.width, canvas.height)
        let dx = 0;
        let dy = 0;
        let width = thing.naturalWidth
        let height = thing.naturalHeight

        // console.log("thesize", width, height)

        if (canvas.width / canvas.height <= width / height) {
          height = height / width * canvas.width;
          width = canvas.width;
          dy = (canvas.height - height) / 2
        } else {
          width = width / height * canvas.height;
          height = canvas.height;
          dx = (canvas.width - width) / 2
        }

        // console.log("resize", dx, dy, height, width)

        return {
          dx: dx,
          dy: dy,
          width: width,
          height: height
        }
      }


    },


  },

  created() {
    this.CONNECT();
  },

}
</script>

<template>
  <v-container>
    <!-- ÊêúÁ¥¢ÂäüËÉΩ -->
    <v-row justify="center">

      <!-- ÊêúÁ¥¢Ê°Ü -->
      <v-col md="4">
        <v-text-field label="ËØ∑ËæìÂÖ•ÊêúÁ¥¢ÂÖ≥ÈîÆÂ≠ó...ÊåâÂõûËΩ¶ÊêúÁ¥†" v-model="keyWord" dense clearable outlined @keyup.enter="search">
        </v-text-field>
      </v-col>

      <!-- ÊêúÁ¥¢ÊåâÈíÆ -->
      <v-col md="2">
        <v-btn @click="search" outlined>
          <v-icon>mdi-magnify</v-icon>ÊêúÁ¥¢‰∏Ä‰∏ã
        </v-btn>
      </v-col>

      <v-col md="1">
        <!-- Â±ïÁ§∫Âä†ËΩΩ‰∏≠ -->
        <div v-show="info.isLoading">
          <!-- <v-progress-circular :size="500" :width="30" color="purple" indeterminate></v-progress-circular> -->
          <v-progress-circular indeterminate color="primary"></v-progress-circular>
        </div>
      </v-col>

    </v-row>


    <!-- Â±ïÁ§∫ÂäüËÉΩ -->
    <v-row>



      <div>
        <!-- Â±ïÁ§∫ËØç -->
        <div>
          <h1 v-show="info.isFirst">ËØ∑ËæìÂÖ•ÊêúÁ¥¢ÂÖ≥ÈîÆÂ≠ó...ÊåâÂõûËΩ¶ËøõË°åÊêúÁ¥¢</h1>
        </div>


        <!-- Â±ïÁ§∫ÈîôËØØ‰ø°ÊÅØ -->
        <h1 v-show="info.errMsg">{{ info.errMsg }}</h1>


        <!-- Â±ïÁ§∫Êú™Êü•ËØ¢Âà∞ÁªìÊûú‰ø°ÊÅØ -->
        <h1 v-show="unsearched">Êú™ÊêúÁ¥¢Âà∞‰ø°ÊÅØ</h1>
      </div>
    </v-row>
    <div>
      <!-- ÂàóË°® -->
      <div class="img-box" v-show="info.users.length" v-for="item in info.users" :key="item.id">

        <div @click="canvasco">
          <v-img @click="updatesdetail(item)" :src="item.src" :aspect-ratio="279.984 / 186.656" />
        </div>

        <span class="res-span">{{ item.res }}</span>

        <span class="down-span">
          <a :href="item.src" target="downloadFile">‰øùÂ≠ò</a>
        </span>

        <span @click="favorite_item(item)" class="like-span">
          <v-btn v-show="favorState(item) == false" icon color="red">
            <v-icon>favorite_border</v-icon>
          </v-btn>
          <v-btn v-show="favorState(item) == true" icon color="red">
            <v-icon>favorite</v-icon>
          </v-btn>
        </span>

      </div>




      <div class="image-background" v-if="isdtpageshow">
        <!-- ÂõæÂÉèÁîªÂ∏É -->
        <canvas></canvas>


        <!-- ÁîªÂ∏É ÂÖ≥Èó≠ÊåâÈíÆ-->
        <span class="close-span">
          <v-icon color="white" @click="closedetailpage">close</v-icon>
        </span>

        <!-- ÂõæÂÉèÂàÜËæ®Áéá -->
        <div class="image-resolution">
          <span>{{ imgselect.res }}</span>
        </div>


        <div class="image-tools-bar">
          <v-btn @click="backone" title="‰∏ä‰∏ÄÂº†" class="tool" text>
            <v-icon>arrow_back</v-icon>
          </v-btn>

          <v-btn @click="favorite_item(imgselect)" v-show="favorState(imgselect) == false" title="ÂñúÊ¨¢" class="tool" text>
            <v-icon color="red">favorite_border</v-icon>
          </v-btn>

          <v-btn @click="favorite_item(imgselect)" v-show="favorState(imgselect) == true" title="ÂñúÊ¨¢" class="tool" text>
            <v-icon color="red">favorite</v-icon>
          </v-btn>


          <v-btn :href="imgselect.src" title="‰∏ãËΩΩ" class="tool" a text>
            <v-icon color="green">file_download</v-icon>
          </v-btn>

          <v-btn @click="toggleFullScreen" v-show="isFull" title="ÂÖ®Â±è" class="tool" text>
            <v-icon color="blue">fullscreen</v-icon>
          </v-btn>

          <v-btn @click="toggleFullScreen" v-show="!isFull" title="ÁªìÊùüÂÖ®Â±è" class="tool" text>
            <v-icon color="blue">fullscreen_exit</v-icon>
          </v-btn>


          <v-btn @click="nextone" title="‰∏ã‰∏ÄÂº†" class="tool" text>
            <v-icon>arrow_forward</v-icon>
          </v-btn>


        </div>
      </div>

    </div>

    <div v-show="info.users.length" class="text-center pagination-bar">
      <v-container>
        <div class="vue_pagination">
          <v-pagination v-model="page" :length="length" @input="$vuetify.goTo(0, 'duration'), lol()"
            prev-icon="arrow_left" next-icon="arrow_right"></v-pagination>
          <hr />

          <v-btn>ÂΩìÂâçÂÖ± {{ this.total }} Âº† / {{ this.length }} È°µ Page = {{ this.page }}</v-btn>
        </div>
      </v-container>
    </div>

  </v-container>
</template>


